import { Field, reduxForm } from "redux-form";
import { requiredField, MaxLengthCreator } from "../../utils/validators"
import { Input, Textarea } from "../FormsControls";
import styles from "../FormsControls.module.css"


const MaxLength20 = MaxLengthCreator(20);
const MaxLength200 = MaxLengthCreator(200);

const ProfileDataForm = (props) => {
    return <div>
        <form onSubmit={props.handleSubmit}>
            <b>Full name:</b>
            <div><Field placeholder="Your name" component={Input} name="fullName" validate={[requiredField, MaxLength20]} /></div>
            <div><Field type={"checkbox"} component="input" name="lookingForAJob" />Looking for a job</div>
            <b>Professional skills:</b>
            <div><Field placeholder="Skills" component={Textarea} name="lookingForAJobDescription" validate={[MaxLength200]} /></div>
            <b>About me:</b>
            <div><Field placeholder="About me" component={Textarea} name="aboutMe" validate={[MaxLength200]} /></div>
            <b>Contacts:</b>
            <div> {Object.keys(props.profile.contacts).map(key => {
                return <div><b>{key}</b> <Field placeholder={key} component={Input} name={"contacts." + key} /> </div>
            })}</div>
             {props.error && <div className={styles.errorForm}>{props.error}</div>} 
            <button>Save</button>
        </form>
        <button onClick={props.disableEditMode}>Cancel</button>
    </div>
};

const ProfileDataReduxForm = reduxForm({
    // a unique name for the form
    form: 'profileDataForm'
})(ProfileDataForm);

export default ProfileDataReduxForm;