import React from 'react';
import clNames from './MyPosts.module.css';
import Post from './Post';
import { Field, reduxForm } from 'redux-form';



function MyPosts(props) {
    let postsElements = [...props.posts].reverse().map(post => <Post message={post.message} likes={post.likes} onDeletePost={props.onDeletePost} postId={post.id}/>);
    const onSubmit = (formData) => { props.onAddPost(formData.post) };
    return <div className={clNames.postsBlock}>
        <PostReduxForm onSubmit={onSubmit}/>
        {postsElements}
    </div>
}
    


const PostForm = (props) => {
    return <div>
        <h3>My post</h3>
        <form onSubmit={props.handleSubmit}>
            <div><Field component="textarea" value={props.newPostText} name="post" /></div>
            <div><button>Add post</button></div>
        </form>
    </div>
}

const PostReduxForm = reduxForm({
    // a unique name for the form
    form: 'post'
})(PostForm)
export default MyPosts;


