import clNames from './MyPosts.module.css';

function Post(props) {
    const deletePost = () => {
        props.onDeletePost(props.postId)
    }
    return (
        <div className={clNames.item}>
            <img src='' />
            {props.message}
            <div><span>likes </span>{props.likes}</div> 
            <button onClick={deletePost} className={clNames.deleteButton}>Удалить</button>
        </div>
    )

}
export default Post; 